body {
    margin: 4vmin 5vw;
    background-color: hsl(195deg 46% 92%);
    color: hsl(0 0% 8%);
    font-size: 3vmin;
}

button {
    border: none;
    font-size: 1.5em;
    margin-left: 2vw;
    background-image: linear-gradient(44deg, hsl(195.79deg 46% 92%), hsl(65deg 100% 60%) 130%);
    border-radius: 0.3em 1em;
    padding: 0.2em;
}

button:hover {
    background-image: linear-gradient(44deg, hsl(195.79deg 46% 92%), hsl(65deg 100% 60%));
    cursor: pointer;
}

input {
    font-size: 1em;
    border-width: 0.2em;
}