.paper {
    background-color: hsl(330deg 80% 93%);
    padding: 20px;
    border-radius: 1em 3em;
}

.imageContainer {
    display: inline-block;
    margin: 20px;
    min-width: 15vw;
}

.image {
    min-height: 200px;
    max-height: 40vmin;
    box-shadow: 0 1px 6px 2px hsl(0deg 0% 0% / 86%);
}

.void {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: hsl(200deg 36% 51% / 44%);
}

.modal {
    position: absolute;
    top: calc(50% - 34px);
    left: calc(50% - 98px);
    border: 7px groove hsl(150 50% 30%);
    padding: 7px;
}